@import 'variables';
@import 'General';

@mixin styling($fs-h3, $fs-body){
  .footer {
    font-size: $fs-h3;
  }
  .footer__link{
    font-size: $fs-body;
  }
}

.footer {
    background-color: black;
    color: $clr-accent;
    font-size: $fs-h3;
    padding: 1em 0;
    text-align: center;
  }
  
  .footer__link,
  .social_list__link {
    color: inherit;
    text-decoration: none;
  }
  
  .footer__link:hover,
  .social_list__link:hover {
    opacity: 0.7;
  }
  
  .footer__link {
    font-weight: $fw-reg;
    font-size: $fs-body;
    margin: 50px 0 5px;
  }
  
  .social_list {
    list-style-type: none;
    display: flex;
    justify-content: center;
    margin: 1em 0 0;
    padding: 0;
  }
  
  .social_list__item {
    margin: 0 0.5em;
  }
  
  .social_list__link {
    padding: 0.25em;
  }

  
@media (min-width: 800px){
    @include styling(
      $fs-h3: 1.5rem,
      $fs-body: 1.125rem
      );
  }