@import 'variables';
@import 'General';

@mixin styling($fs-h3){
  .logo {
    font-size: $fs-h3;
  }
}

/* Header */
.header {
    display: flex;
    justify-content: space-between;
    padding: 0.5em 1em;
  }
  
  .logo {
    color: $clr-accent;
    font-family: $ff-primary;
    font-weight: $fw-bold;
    font-size: $fs-h3;
    max-width: 150px;
  }
  
  header nav {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 100;
    background: $clr-dark;
    color: $clr-light;
    transform: translateX(100%);
    transition: transform 500ms cubic-bezier(0.5, 0, 0.5, 1);
  }
  
  .navList {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 100%;
    margin: 0;
    padding: 0;
  }
  
  .navLink {
    text-decoration: none;
    color: inherit;
    font-weight: $fw-bold;
  }
  
  .navLink:hover {
    color: $clr-accent;
  }
  
  .navItem {
    transition: transform 500ms linear;
  }
  
  .navItem:hover {
    transform: scale(1.3);
  }
  
  .navToggle {
    cursor: pointer;
    padding: 0.25em;
    background: transparent;
    border-style: none;
    position: absolute;
    right: 1em;
    top: 1em;
    z-index: 1000;
  }
  
  .navOpen .nav {
    transform: translateX(0%);
  }
  
  .navOpen .navToggle {
    position: fixed;
  }
  
  .navOpen .hamburger {
    transform: rotate(0.625turn);
  }
  
  .navOpen .hamburger::before {
    transform: rotate(90deg) translateX(6px);
  }
  
  .navOpen .hamburger::after {
    opacity: 0;
  }
  
  .hamburger {
    display: block; /* as width/height will not work with inline/span */
    width: 1.5em;
    height: 3px;
    border-radius: 10em;
    background: $clr-accent;
    position: relative;
  }
  
  .hamburger::before {
    content: "";
    height: 3px;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 6px;
    background: $clr-accent;
    border-radius: 1em;
  }
  
  .hamburger::after {
    content: "";
    height: 3px;
    position: absolute;
    left: 0;
    right: 0;
    top: 6px;
    background: $clr-accent;
    border-radius: 1em;
  }
  
  @media screen and (min-width: 600px) {
    .navList .downloadResume {
      display: none;
    }
  }
  /* Header End*/
  


@media (min-width: 800px){
  @include styling($fs-h3: 1.5rem);
}