@import 'variables';
@import 'General';

.about_me {
    max-width: 1000px;
    margin: 0 auto;
  }
  
.about_me__img {
    box-shadow: $bs;
    width: 400px;
    height: 400px;
  }
  
  @media screen and (min-width: 600px) {
    .about_me {
      display: grid;
      margin: 0 auto;
      grid-column-gap: 1em;
      grid-template-areas:
        "title img"
        "subtitle img"
        "body img";
      grid-template-columns: 1fr 300px;
    }
  
    .section__title__about {
      grid-area: title;
    }
  
    .section__subtitle__about {
      grid-area: subtitle;
      grid-column: 1 / -1;
      grid-row: 2;
      align-self: start;
      position: relative;
      left: -1em;
      width: calc(100% + 1em + 1em);
      padding-left: 1em;
      padding-right: calc(300px + 1em + 1em);
      animation: slideright 1.5s ease;
    }
  
    @keyframes slideright {
      0% {
        opacity: 0;
        transform: translateX(-80px);
      }
  
      100% {
        opacity: 1;
        transform: translateX(0px);
      }
    }
  
    .about_me__body {
      grid-area: body;
      animation: slideup 1.5s ease;
    }
  
    @keyframes slideup {
      0% {
        opacity: 0;
        transform: translateY(80px);
      }
  
      100% {
        opacity: 1;
        transform: translateY(0px);
      }
    }
  
    .about_me__img {
      grid-area: img;
      grid-row: -1 / 1;
      grid-column: 2 / 3;
      position: relative;
      z-index: 2;
      animation: slideleft 1.5s ease;
    }
  
    @keyframes slideleft {
      0% {
        opacity: 0;
        transform: translateX(80px);
      }
  
      100% {
        opacity: 1;
        transform: translateX(0px);
      }
    }
  }
  