@import 'variables';
@import 'General';

.my_work {
    background-color: $clr-dark;
    color: $clr-light;
    text-align: center;
  }
  
  .section__subtitle__work {
    color: $clr-accent;
    font-weight: $fw-bold;
    margin-bottom: 2em;
  }
  
  .porfolio {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 10px;
  }
  
  .portfolio__item {
    background: $clr-accent;
    overflow: hidden;
    position: relative;
  }
  
  .portfolio__img {
    transition: transform 700ms cubic-bezier(0.5, 0, 0.5, 1), opacity 250ms linear;
    border-radius: 10px;
    width: 100%;
  }
  
  .image_txt {
    color: $clr-dark;
    position: absolute;
    top: 7rem;
    left: 6rem;
    opacity: 0;
    transition: opacity 700ms linear;
  }
  
  .portfolio__item:hover .image_txt {
    opacity: 1;
  }
  
  .portfolio__item:focus {
    position: relative;
    z-index: 2;
  }
  
  .portfolio__img:hover,
  .portfolio__item:focus .portfolio__img {
    transform: scale(1.2);
    opacity: 0.75;
  }
  