@import 'variables';
@import 'General';


/* My Services */
.my_services {
    background-color: $clr-dark; 
    background-image: url("../assets/images/servicesbg.jpg");
    background-size: cover;
    text-align: center;
    color: $clr-light;
  }
  
  .section_title__services {
    color: $clr-accent;
    position: relative;
  }
  
  .section_title__services::after {
    content: "";
    display: block;
    width: 3em;
    height: 1px;
    background: $clr-light;
    margin: 0.5em auto 1em;
    opacity: 0.25;
  }
  
  .services {
    margin-bottom: 4em;
  }
  
  @media screen and (min-width: 800px) {
    .services {
      display: flex;
      max-width: 1000px;
      margin-left: auto;
      margin-right: auto;
    }
  }
  
  /* My Services End*/
  