@import 'variables';
@import 'General';

.service {
    max-width: 400px;
    margin: 1em auto;
    padding: 1em;
    border: 1px solid $clr-accent;
    border-radius: 10px;
  }
  

@media screen and (min-width: 800px) {  
    .service {
      margin: 0 auto;
    }
  
    .service + .service {
      margin-left: 2em;
    }
  }