@import 'variables';
@import 'General';

/* Intro Section */
/* Box animation */
.box div:nth-child(1) {
    top: 20%;
    left: 42%;
    animation: animatebox 3s linear infinite;
  }
  
  .box div:nth-child(2) {
    top: 25%;
    left: 10%;
    animation: animatebox 4s linear infinite;
  }
  
  .box div:nth-child(3) {
    top: 30%;
    left: 60%;
    animation: animatebox 3500ms linear infinite;
  }
  
  .box div:nth-child(4) {
    top: 35%;
    left: 20%;
    animation: animatebox 4500ms linear infinite;
  }
  
  .box div:nth-child(5) {
    top: 70%;
    left: 8%;
    animation: animatebox 3s linear infinite;
  }
  
  .box div:nth-child(6) {
    top: 70%;
    right: 8%;
    animation: animatebox 4s linear infinite;
  }
  
  .box div:nth-child(7) {
    top: 70%;
    right: 25%;
    animation: animatebox 3s linear infinite;
  }
  
  .box div:nth-child(8) {
    top: 40%;
    right: 20%;
    animation: animatebox 2s linear infinite;
  }
  
  .box div:nth-child(9) {
    top: 20%;
    right: 11%;
    animation: animatebox 4s linear infinite;
  }
  
  .box div:nth-child(10) {
    top: 10%;
    right: 25%;
    animation: animatebox 2s linear infinite;
  }
  
  .box div:nth-child(11) {
    top: 45%;
    left: 5%;
    animation: animatebox 7s linear infinite;
  }
  
  .box div {
    position: absolute;
    height: 50px;
    width: 50px;
    border: 3px solid $clr-accent;
    border-radius: 8px;
    animation: animatebox 6s linear infinite;
  }
  
  @keyframes animatebox {
    0% {
      opacity: 1;
      transform: translateY(0) scale(0) rotate(0);
    }
  
    100% {
      opacity: 0;
      transform: translateY(-80px) scale(1.5) rotate(90deg);
    }
  }
  
  /* Box animation End*/
  .intro {
    position: relative;
    /* background: linear-gradient(to bottom, #e1ffff 0%,#e1ffff 7%,#e1ffff 12%,#fdffff 39%,#e6f8fd 71%,
                                #e6f8fd 73%,#bee4f8 90%,#b1d8f5 94%,#b1d8f5 94%,#0de2d4 97%,#c8eefb 97%); */
    background: linear-gradient(
      to bottom,
      $clr-accent 0%,
      $clr-accent 7%,
      $clr-accent 12%,
      #fdffff 39%,
      #e6f8fd 71%,
      #e6f8fd 73%,
      $clr-accent 90%,
      $clr-accent 94%,
      $clr-accent 94%,
      $clr-accent 97%,
      $clr-accent 97%
    );
  }
  
  .intro__img {
    box-shadow: $bs;
  }
  
  .section__subtitle__intro {
    display: inline-block;
  }
  
  .section__subtitle__download {
    display: none;
    animation: download 600ms linear infinite;
  }
  
  @keyframes download {
    0% {
      transform: scale(1);
    }
  
    100% {
      transform: scale(1.3);
    }
  }
  
  @media screen and (min-width: 600px) {
    .intro {
      display: grid;
      width: 100%;
      margin: 0 auto;
      justify-content: center;
      grid-column-gap: 1em;
      grid-template-areas:
        "img title"
        "img subtitle";
      grid-template-columns: min-content max-content;
    }
  
    .intro__img {
      grid-area: img;
      min-width: 250px;
      position: relative;
      z-index: 2;
      animation: slideright 1.5s ease;
    }
  
    @keyframes slideright {
      0% {
        opacity: 0;
        transform: translateX(-80px);
      }
  
      100% {
        opacity: 1;
        transform: translateX(0px);
      }
    }
  
    .section__subtitle__intro {
      align-self: start;
      grid-column: -1 / 1;
      grid-row: 2;
      text-align: right;
      position: relative;
      left: -1.5em;
      width: calc(100% + 1.5em);
      animation: slideleft 1.5s ease;
    }
  
    @keyframes slideleft {
      0% {
        opacity: 0;
        transform: translateX(80px);
      }
  
      100% {
        opacity: 1;
        transform: translatX(0px);
      }
    }
    .section__subtitle__download {
      display: block;
      position: absolute;
      left: 1em;
      bottom: 2em;
      color: $clr-dark;
      opacity: 0.5;
    }
  }
  
  /* Intro Section End*/
  
  