@import 'variables';

@mixin styling($fs-body, $fs-h1, $fs-h2, $fs-h3){
  body{
    font-size: $fs-body;
  }

  h1{
    font-size: $fs-h1;
  }

  h2{
    font-size: $fs-h2;
  }

  h3{
    font-size: $fs-h3;
  }

  .section__subtitle {
    font-size: $fs-h3;
  }
}


*,
*::before,
*::after {
  box-sizing: border-box;
}

/* General Styles */
html {
    scroll-behavior: smooth;
  }
  
  body {
    background: $clr-light;
    color: $clr-dark;
    margin: 0;
    font-family: $ff-primary;
    font-size: $fs-body;
    line-height: 1.6;
  }
  
  img {
    display: block;
    max-width: 100%;
  }
  
  :focus {
    outline: 2px solid $clr-accent;
    outline-offset: 3px;
  }
  
  section {
    padding: 5em 2em;
  }
  
  strong {
    font-weight: $fw-bold;
  }


/* Buttons */

.btn {
  display: inline-block;
  color: $clr-dark;
  text-decoration: none;
  background: $clr-accent;
  font-size: 0.8rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: $fw-bold;
  padding: 0.5em 2.5em;
  transition: transform 200ms ease-in-out;
}

.btn:hover {
  transform: scale(1.5);
}

.btnpost {
  display: inline-block;
  color: $clr-dark;
  text-decoration: none;
  background: $clr-accent;
  font-size: 0.8rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: $fw-bold;
  padding: 0.5em 2.5em;
}

/* Typography */
h1,
h2,
h3 {
  line-height: 1.1;
  margin: 0;
}

h1 {
  font-size: $fs-h1;
}
h2 {
  font-size: $fs-h2;
}
h3 {
  font-size: $fs-h3;
}

.section__title {
  margin-bottom: 0.25rem;
  animation: slidedown 1.5s ease;
}

@keyframes slidedown {
  0% {
    opacity: 0;
    transform: translateY(-80px);
  }

  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}

.section__title__intro {
  font-weight: $fw-reg;
}

.section__title__intro strong {
  display: block;
}

.section__subtitle {
  margin: 0;
  font-size: $fs-h3;
}

.section__subtitle__intro,
.section__subtitle__about {
  background: $clr-accent;
  padding: 0.25em 1em;
  font-family: $ff-secondary;
  margin-bottom: 1em;
}



@media (min-width: 800px){
  @include styling(
    $fs-body: 1.125rem,
    $fs-h1: 4.5rem,
    $fs-h2: 3.75rem,
    $fs-h3: 1.5rem,
    );
}