@import 'variables';
@import 'General';

table {
    max-width: 1000px;
    width: 60%;
    margin: 5em auto;
    outline: 3px solid $clr-dark;
    outline-offset: 2px;
  }
  
  table,
  th,
  td {
    border: 1px solid $clr-dark;
    border-collapse: collapse;
    text-align: center;
  }
  
  th,
  td {
    padding: 1em 0;
    margin: 0 aut0;
  }
  
  tbody tr:nth-child(odd) {
    background-color: #dddddd;
  }